import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { LettersService } from '../../../services/letters.service';
import { LetterItem, LetterModel } from '../../../models/letter.model';
import { TranslationService } from '../../../services/translation.service';
import { LettersConfirmationModalComponent } from '../letters-confirmation-modal/letters-confirmation-modal.component';
import { SignaturePadModalComponent } from '../signature-pad-modal/signature-pad-modal.component';
import { RejectionModalComponent } from '../rejection-modal/rejection-modal.component';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-letters-approval',
  standalone: false,
  templateUrl: './letters-approval.component.html',
  styleUrl: './letters-approval.component.css'
})
export class LettersApprovalComponent implements OnInit, OnDestroy {
  @ViewChild('confirmationModal') confirmationModal!: LettersConfirmationModalComponent;
  @ViewChild('signaturePadModal') signaturePadModal!: SignaturePadModalComponent;
  @ViewChild('rejectionModal') rejectionModal!: RejectionModalComponent;
  
  pendingLetters: LetterItem[] = [];
  isLoading: boolean = false;
  isProcessing: boolean = false;
  processingLetterId: number | null = null;
  private destroy$ = new Subject<void>();

  constructor(
    private lettersService: LettersService,
    private translationService: TranslationService
  ) {}

  ngOnInit() {
    this.loadPendingLetters();
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  loadPendingLetters() {
    this.isLoading = true;
    
    this.lettersService.getLettersForApproval().pipe(
      takeUntil(this.destroy$)
    ).subscribe({
      next: (res) => {
        console.log('Pending letters loaded:', res.data.items);
        this.pendingLetters = res.data.items;
        this.isLoading = false;
      },
      error: (error) => {
        console.error('Error loading pending letters:', error);
        this.isLoading = false;
      }
    });
  }

  async approveLetter(letter: LetterItem) {
    if (this.isProcessing || this.processingLetterId === letter.id) {
      return;
    }

    // Set the letter ID for processing
    this.processingLetterId = letter.id;
    
    // Open signature pad modal
    this.signaturePadModal.open();
  }

  onSignatureSaved(data: {signature: File, approvalNotes?: string}) {
    if (!this.processingLetterId) return;

    this.isProcessing = true;
    
    this.lettersService.approveLetter(this.processingLetterId, data.signature, data.approvalNotes).pipe(
      takeUntil(this.destroy$)
    ).subscribe({
      next: (res) => {
        console.log('Letter approved successfully');
        this.signaturePadModal.close(); // Close the signature pad modal
        this.showSuccessMessage(this.translate('letters.approve_success'));
        this.removeLetterFromList(this.processingLetterId!);
        this.isProcessing = false;
        this.processingLetterId = null;
      },
      error: (error) => {
        console.error('Error approving letter:', error);
        const errorMessage = error.error?.message || error.message || 'Unknown error occurred';
        this.showErrorMessage(this.translate('common.error') + ': ' + errorMessage);
        this.isProcessing = false;
        this.processingLetterId = null;
      }
    });
  }

  async rejectLetter(letter: LetterItem) {
    if (this.isProcessing || this.processingLetterId === letter.id) {
      return;
    }

    // Set the letter ID for processing
    this.processingLetterId = letter.id;
    
    // Open rejection modal
    this.rejectionModal.open();
  }

  onRejectionConfirmed(rejectionReason: string) {
    if (!this.processingLetterId) return;

    this.isProcessing = true;
    
    this.lettersService.rejectLetter(this.processingLetterId, rejectionReason).pipe(
      takeUntil(this.destroy$)
    ).subscribe({
      next: (res) => {
        console.log('Letter rejected successfully');
        this.showSuccessMessage(this.translate('letters.reject_success'));
        this.removeLetterFromList(this.processingLetterId!);
        this.isProcessing = false;
        this.processingLetterId = null;
      },
      error: (error) => {
        console.error('Error rejecting letter:', error);
        const errorMessage = error.error?.message || error.message || 'Unknown error occurred';
        this.showErrorMessage(this.translate('common.error') + ': ' + errorMessage);
        this.isProcessing = false;
        this.processingLetterId = null;
      }
    });
  }

  async sendEmail(letter: LetterItem) {
    if (this.isProcessing || this.processingLetterId === letter.id) {
      return;
    }

    const confirmData = {
      title: this.translate('letters.send_email'),
      message: this.translate('letters.confirm_send_email'),
      confirmText: this.translate('letters.send_email'),
      cancelText: this.translate('common.cancel'),
      type: 'info' as const
    };

    try {
      const confirmed = await this.confirmationModal.show(confirmData);
      
      if (!confirmed) {
        return;
      }

      this.isProcessing = true;
      this.processingLetterId = letter.id;
      
      this.lettersService.sendEmail(letter.id).pipe(
        takeUntil(this.destroy$)
      ).subscribe({
        next: (res) => {
          console.log('Email sent successfully');
          this.showSuccessMessage(this.translate('letters.send_email_success'));
          this.isProcessing = false;
          this.processingLetterId = null;
        },
        error: (error) => {
          console.error('Error sending email:', error);
          const errorMessage = error.error?.message || error.message || 'Unknown error occurred';
          this.showErrorMessage(this.translate('common.error') + ': ' + errorMessage);
          this.isProcessing = false;
          this.processingLetterId = null;
        }
      });
    } catch (error) {
      console.error('Error in send email confirmation:', error);
      this.isProcessing = false;
      this.processingLetterId = null;
    }
  }

  downloadPdf(letter: LetterItem) {
    this.lettersService.downloadLetterPdf(letter.id).pipe(
      takeUntil(this.destroy$)
    ).subscribe({
      next: (blob) => {
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `Letter_${letter.id}.pdf`;
        link.click();
        window.URL.revokeObjectURL(url);
        this.showSuccessMessage(this.translate('letters.download_pdf_success'));
      },
      error: (error) => {
        console.error('Error downloading PDF:', error);
        if (error.status === 404) {
          this.showErrorMessage(this.translate('letters.no_pdf_available'));
        } else {
          const errorMessage = error.error?.message || error.message || 'Unknown error occurred';
          this.showErrorMessage(this.translate('common.error') + ': ' + errorMessage);
        }
      }
    });
  }

  private removeLetterFromList(letterId: number) {
    this.pendingLetters = this.pendingLetters.filter(letter => letter.id !== letterId);
  }

  showSuccessMessage(message: string) {
    const successData = {
      title: this.translate('common.success'),
      message: message,
      confirmText: this.translate('common.ok'),
      cancelText: '',
      type: 'info' as const
    };
    this.confirmationModal.show(successData);
  }

  showErrorMessage(message: string) {
    const errorData = {
      title: this.translate('common.error'),
      message: message,
      confirmText: this.translate('common.ok'),
      cancelText: '',
      type: 'warning' as const
    };
    this.confirmationModal.show(errorData);
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  formatDate(dateString: string): string {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleDateString();
  }

  getDirectionLabel(direction: number): string {
    return this.translate('letters.directions.' + direction);
  }

  getConfidentialityLabel(confidentiality: number): string {
    return this.translate('letters.confidentialities.' + confidentiality);
  }

  getStatusLabel(status: number): string {
    return this.translate('letters.statuses.' + status);
  }

  getDirectionBadgeClass(direction: number): string {
    return direction === 0 ? 'bg-primary' : 'bg-success';
  }

  getConfidentialityBadgeClass(confidentiality: number): string {
    switch (confidentiality) {
      case 0: return 'bg-success';
      case 1: return 'bg-warning';
      case 2: return 'bg-danger';
      default: return 'bg-secondary';
    }
  }

  getStatusBadgeClass(status: number): string {
    switch (status) {
      case 0: return 'bg-secondary';
      case 1: return 'bg-info';
      case 2: return 'bg-success';
      case 3: return 'bg-dark';
      case 4: return 'bg-danger';
      default: return 'bg-secondary';
    }
  }
}
