import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import {} from "../models/letter.model"
import { 
  LetterModel, 
  LetterItem, 
  CreateLetterDto, 
  UpdateLetterDto, 
  LetterSearchParams,
  LetterAttachment,
  AttachmentUploadDto,
  SubmitForApprovalResponse,
  ApproveLetterRequest,
  RejectLetterRequest,
  SendEmailRequest
} from '../models/letter.model';

@Injectable({
  providedIn: 'root'
})
export class LettersService {
  
  private apiUrl = `${environment.apiBaseUrl}/Letters`;
  
  constructor(private http: HttpClient) { }

  getAllLetters(params: LetterSearchParams = {}): Observable<LetterModel> {
    let url = this.apiUrl;
    const queryParams = new URLSearchParams();
    
    if (params.search) queryParams.append('search', params.search);
    if (params.PageSize) queryParams.append('PageSize', params.PageSize.toString());
    if (params.PageNumber) queryParams.append('PageNumber', params.PageNumber.toString());
    if (params.Confidentiality !== undefined) queryParams.append('Confidentiality', params.Confidentiality.toString());
    if (params.Direction !== undefined) queryParams.append('Direction', params.Direction.toString());
    if (params.From) queryParams.append('From', params.From);
    if (params.To) queryParams.append('To', params.To);
    
    if (queryParams.toString()) {
      url += '?' + queryParams.toString();
    }
    
    return this.http.get<LetterModel>(url);
  }

  getLetter(id: number): Observable<{ success: boolean; message: string; data: LetterItem; errors: any[] }> {
    return this.http.get<{ success: boolean; message: string; data: LetterItem; errors: any[] }>(`${this.apiUrl}/${id}`);
  }

  createLetter(letter: CreateLetterDto): Observable<any> {
    return this.http.post<any>(this.apiUrl, letter);
  }

  updateLetter(id: number, letter: UpdateLetterDto): Observable<any> {
    return this.http.put<any>(`${this.apiUrl}/${id}`, letter);
  }

  deleteLetter(id: number): Observable<any> {
    return this.http.delete<any>(`${this.apiUrl}/${id}`);
  }

  // Attachment methods
  addAttachment(letterId: number, attachment: AttachmentUploadDto): Observable<any> {
    const formData = new FormData();
    formData.append('File', attachment.file);
    if (attachment.description) {
      formData.append('Description', attachment.description);
    }
    if (attachment.documentSource !== undefined) {
      formData.append('DocumentSource', attachment.documentSource.toString());
    }

    return this.http.post<any>(`${this.apiUrl}/${letterId}/attachments`, formData);
  }

  deleteAttachment(letterId: number, attachmentId: number): Observable<any> {
    return this.http.delete<any>(`${this.apiUrl}/${letterId}/attachments/${attachmentId}`);
  }

  updateAttachment(letterId: number, attachmentId: number, attachment: Partial<LetterAttachment>): Observable<any> {
    return this.http.put<any>(`${this.apiUrl}/${letterId}/attachments/${attachmentId}`, attachment);
  }

  getLetterAttachments(letterId: number): Observable<any> {
    return this.http.get<any>(`${this.apiUrl}/${letterId}/attachments`);
  }

  // New approval workflow methods
  submitForApproval(id: number): Observable<SubmitForApprovalResponse> {
    return this.http.post<SubmitForApprovalResponse>(`${this.apiUrl}/${id}/submit-for-approval`, {});
  }

  getLettersForApproval(): Observable<LetterModel> {
    return this.http.get<LetterModel>(`${this.apiUrl}/approval`);
  }

  approveLetter(id: number, signature: File, approvalNotes?: string): Observable<any> {
    const formData = new FormData();
    formData.append('SignatureImage', signature);
    if (approvalNotes && approvalNotes.trim()) {
      formData.append('ApprovalNotes', approvalNotes.trim());
    }
    return this.http.post<any>(`${this.apiUrl}/${id}/approve`, formData);
  }

  rejectLetter(id: number, rejectionReason?: string): Observable<any> {
    const body = rejectionReason ? { rejectionReason } : {};
    return this.http.post<any>(`${this.apiUrl}/${id}/reject`, body);
  }

  sendEmail(id: number, emailData?: SendEmailRequest): Observable<any> {
    return this.http.post<any>(`${this.apiUrl}/${id}/send-email`, emailData || {});
  }

  downloadLetterPdf(letterId: number): Observable<Blob> {
    return this.http.get(`${this.apiUrl}/${letterId}/download-pdf`, { responseType: 'blob' });
  }
}
