import { Component, EventEmitter, Input, Output, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TranslationService } from '../../../services/translation.service';

@Component({
  selector: 'app-signature-pad-modal',
  standalone: false,
  templateUrl: './signature-pad-modal.component.html',
  styleUrl: './signature-pad-modal.component.css'
})
export class SignaturePadModalComponent implements AfterViewInit {
  @ViewChild('signatureCanvas', { static: false }) signatureCanvas!: ElementRef<HTMLCanvasElement>;
  @Input() isOpen = false;
  @Output() signatureSaved = new EventEmitter<{signature: File, approvalNotes?: string}>();
  @Output() modalClosed = new EventEmitter<void>();

  private canvas!: HTMLCanvasElement;
  private ctx!: CanvasRenderingContext2D;
  private isDrawing = false;
  private lastX = 0;
  private lastY = 0;
  private uploadedFile: File | null = null;
  
  approvalForm: FormGroup;

  constructor(
    private translationService: TranslationService,
    private fb: FormBuilder
  ) {
    this.approvalForm = this.fb.group({
      approvalNotes: ['']
    });
  }

  ngAfterViewInit() {
    if (this.signatureCanvas) {
      this.initializeCanvas();
    }
  }

  open() {
    this.isOpen = true;
    this.approvalForm.reset();
    setTimeout(() => {
      this.initializeCanvas();
    }, 100);
  }

  close() {
    this.isOpen = false;
    this.clearSignature();
    this.uploadedFile = null;
    this.approvalForm.reset();
    this.modalClosed.emit();
  }

  private initializeCanvas() {
    if (!this.signatureCanvas) return;
    
    this.canvas = this.signatureCanvas.nativeElement;
    this.ctx = this.canvas.getContext('2d')!;
    
    // Set canvas size
    this.canvas.width = 600;
    this.canvas.height = 200;
    
    // Set drawing styles
    this.ctx.strokeStyle = '#000000';
    this.ctx.lineWidth = 2;
    this.ctx.lineCap = 'round';
    this.ctx.lineJoin = 'round';
    
    // Set white background
    this.ctx.fillStyle = '#ffffff';
    this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
    
    // Add event listeners
    this.addEventListeners();
  }

  private addEventListeners() {
    // Mouse events
    this.canvas.addEventListener('mousedown', this.startDrawing.bind(this));
    this.canvas.addEventListener('mousemove', this.draw.bind(this));
    this.canvas.addEventListener('mouseup', this.stopDrawing.bind(this));
    this.canvas.addEventListener('mouseout', this.stopDrawing.bind(this));

    // Touch events for mobile
    this.canvas.addEventListener('touchstart', this.handleTouch.bind(this));
    this.canvas.addEventListener('touchmove', this.handleTouch.bind(this));
    this.canvas.addEventListener('touchend', this.stopDrawing.bind(this));
  }

  private startDrawing(e: MouseEvent) {
    this.isDrawing = true;
    const rect = this.canvas.getBoundingClientRect();
    this.lastX = e.clientX - rect.left;
    this.lastY = e.clientY - rect.top;
  }

  private draw(e: MouseEvent) {
    if (!this.isDrawing) return;

    const rect = this.canvas.getBoundingClientRect();
    const currentX = e.clientX - rect.left;
    const currentY = e.clientY - rect.top;

    this.ctx.beginPath();
    this.ctx.moveTo(this.lastX, this.lastY);
    this.ctx.lineTo(currentX, currentY);
    this.ctx.stroke();

    this.lastX = currentX;
    this.lastY = currentY;
  }

  private stopDrawing() {
    this.isDrawing = false;
  }

  private handleTouch(e: TouchEvent) {
    e.preventDefault();
    const touch = e.touches[0];
    const mouseEvent = new MouseEvent(e.type === 'touchstart' ? 'mousedown' : 'mousemove', {
      clientX: touch.clientX,
      clientY: touch.clientY
    });
    this.canvas.dispatchEvent(mouseEvent);
  }

  clearSignature() {
    if (this.ctx) {
      this.ctx.fillStyle = '#ffffff';
      this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
    }
    this.uploadedFile = null;
  }

  onFileSelected(event: any) {
    const file = event.target.files[0];
    if (file && file.type.startsWith('image/')) {
      this.uploadedFile = file;
      this.loadImageToCanvas(file);
    } else {
      alert(this.translate('letters.invalid_image_format'));
    }
  }

  private loadImageToCanvas(file: File) {
    const reader = new FileReader();
    reader.onload = (e: any) => {
      const img = new Image();
      img.onload = () => {
        // Clear canvas
        this.ctx.fillStyle = '#ffffff';
        this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
        
        // Calculate scaling to fit image in canvas
        const scale = Math.min(this.canvas.width / img.width, this.canvas.height / img.height);
        const x = (this.canvas.width - img.width * scale) / 2;
        const y = (this.canvas.height - img.height * scale) / 2;
        
        // Draw image
        this.ctx.drawImage(img, x, y, img.width * scale, img.height * scale);
      };
      img.src = e.target.result;
    };
    reader.readAsDataURL(file);
  }

  saveSignature() {
    const approvalNotes = this.approvalForm.get('approvalNotes')?.value?.trim();
    
    // If user uploaded a file, use that directly
    if (this.uploadedFile) {
      this.signatureSaved.emit({
        signature: this.uploadedFile,
        approvalNotes: approvalNotes
      });
      this.close();
      return;
    }

    // Otherwise, check if canvas has any content
    if (!this.canvas) return;

    const imageData = this.ctx.getImageData(0, 0, this.canvas.width, this.canvas.height);
    const hasContent = imageData.data.some((channel, index) => {
      return index % 4 !== 3 && channel !== 255; // Check if any pixel is not white (excluding alpha)
    });

    if (!hasContent) {
      alert(this.translate('letters.signature_required'));
      return;
    }

    // Convert canvas to blob
    this.canvas.toBlob((blob) => {
      if (blob) {
        const file = new File([blob], 'signature.png', { type: 'image/png' });
        this.signatureSaved.emit({
          signature: file,
          approvalNotes: approvalNotes
        });
        this.close();
      }
    }, 'image/png');
  }

  onBackdropClick(event: Event) {
    if (event.target === event.currentTarget) {
      this.close();
    }
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }
}
