import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { LettersService } from '../../../services/letters.service';
import { LetterItem, LetterModel, LetterDirection, LetterConfidentiality, LetterStatus, LetterSearchParams } from '../../../models/letter.model';
import { TranslationService } from '../../../services/translation.service';
import { LettersConfirmationModalComponent } from '../letters-confirmation-modal/letters-confirmation-modal.component';
import { AddLetter } from '../add-letter/add-letter';
import { EditLetter } from '../edit-letter/edit-letter';
import { AddAttachment } from '../add-attachment/add-attachment';
import { EditAttachment } from '../edit-attachment/edit-attachment';
import { SignaturePadModalComponent } from '../signature-pad-modal/signature-pad-modal.component';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-letters-list',
  standalone: false,
  templateUrl: './letters-list.html',
  styleUrl: './letters-list.css'
})
export class LettersList implements OnInit, OnDestroy {
  @ViewChild('addLetterModal') addLetterModal!: AddLetter;
  @ViewChild('editLetterModal') editLetterModal!: EditLetter;
  @ViewChild('addAttachmentModal') addAttachmentModal!: AddAttachment;
  @ViewChild('editAttachmentModal') editAttachmentModal!: EditAttachment;
  @ViewChild('confirmationModal') confirmationModal!: LettersConfirmationModalComponent;
  @ViewChild('signaturePadModal') signaturePadModal!: SignaturePadModalComponent;
  
  // Make LetterStatus available in template
  LetterStatus = LetterStatus;
  
  allLetters: LetterItem[] = [];
  filteredLetters: LetterItem[] = [];
  searchTerm: string = '';
  isLoading: boolean = false;
  isDeleting: boolean = false;
  deletingLetterId: number | null = null;
  expandedAttachments: { [key: number]: boolean } = {};
  sentEmails: Set<number> = new Set(); // Track which letters have had emails sent
  isProcessing: boolean = false;
  processingLetterId: number | null = null;
  private destroy$ = new Subject<void>();
  
  // Pagination
  currentPage = 1;
  pageSize = 10;
  totalCount = 0;
  
  // Filters
  selectedDirection: number | null = null;
  selectedConfidentiality: number | null = null;
  selectedStatus: number | null = null;
  fromDate: string = '';
  toDate: string = '';

  constructor(
    private lettersService: LettersService,
    private translationService: TranslationService
  ) {}

  readonly directions: Record<number, string> = {
    0: "Incoming",
    1: "Outgoing"
  }

  readonly confidentialities: Record<number, string> = {
    0: "Public",
    1: "Restricted", 
    2: "Secret"
  }

  readonly statuses: Record<number, string> = {
    0: "Draft",
    1: "Approved",
    2: "Sent",
    3: "Archived",
    4:""
  }

  ngOnInit() {
    this.loadLetters();
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  loadLetters() {
    this.isLoading = true;
    
    const params: LetterSearchParams = {
      search: this.searchTerm || undefined,
      PageSize: this.pageSize,
      PageNumber: this.currentPage,
      Direction: this.selectedDirection || undefined,
      Confidentiality: this.selectedConfidentiality || undefined
    };
    
    this.lettersService.getAllLetters(params).subscribe({
      next: (res) => {
        console.log('Letters loaded:', res.data.items);
        this.allLetters = res.data.items;
        this.totalCount = res.data.totalCount;
        this.currentPage = res.data.page;
        this.pageSize = res.data.pageSize;
        this.filteredLetters = [...this.allLetters];
        this.isLoading = false;
      },
      error: (error) => {
        console.error('Error loading letters:', error);
        this.isLoading = false;
      }
    });
  }

  onSearchChange() {
    this.currentPage = 1;
    this.loadLetters();
  }

  onSearchClick() {
    this.currentPage = 1;
    this.loadLetters();
  }

  onFilterChange() {
    this.currentPage = 1;
    this.loadLetters();
  }

  onPageChange(page: number) {
    this.currentPage = page;
    this.loadLetters();
  }

  onPageSizeChange() {
    this.currentPage = 1;
    this.loadLetters();
  }

  clearFilters() {
    this.searchTerm = '';
    this.selectedDirection = null;
    this.selectedConfidentiality = null;
    this.selectedStatus = null;
    this.fromDate = '';
    this.toDate = '';
    this.currentPage = 1;
    this.loadLetters();
  }

  getDirectionLabel(direction: number): string {
    return this.directions[direction] || 'Unknown';
  }

  getConfidentialityLabel(confidentiality: number): string {
    return this.confidentialities[confidentiality] || 'Unknown';
  }

  getStatusLabel(status: number): string {
    return this.statuses[status] || 'Unknown';
  }

  getAttachmentsDisplay(attachments: any[]): string {
    if (!attachments || attachments.length === 0) {
      return '';
    }
    
    const displayAttachments = attachments.slice(0, 2);
    const attachmentStrings = displayAttachments.map(attachment => 
      this.getAttachmentDisplayName(attachment)
    );
    
    if (attachments.length > 2) {
      return attachmentStrings.join(', ') + ` +${attachments.length - 2} more`;
    }
    
    return attachmentStrings.join(', ');
  }

  getAttachmentDisplayName(attachment: any): string {
    if (attachment.fileName && attachment.fileName.trim()) {
      return attachment.fileName;
    }
    
    // Try to extract filename from filePath if fileName is not available
    if (attachment.filePath) {
      const pathParts = attachment.filePath.split('/');
      const fileName = pathParts[pathParts.length - 1];
      if (fileName && fileName.trim()) {
        return fileName;
      }
    }
    
    // Fallback to a generic name
    return 'Document';
  }

  openAddModal() {
    this.addLetterModal.open();
  }

  async deleteLetter(id: number, subject: string) {
    // Check if letter is already being deleted
    if (this.isDeleting || this.deletingLetterId === id) {
      console.log('Delete operation already in progress for letter:', id);
      return;
    }

    // Check if letter still exists in the list
    const letterExists = this.allLetters.some(l => l.id === id);
    if (!letterExists) {
      console.log('Letter not found in list:', id);
      return;
    }

    const confirmData = {
      title: this.translationService.translate('letters.delete_letter'),
      message: this.translationService.translate('letters.confirm_delete_letter'),
      confirmText: this.translationService.translate('common.delete'),
      cancelText: this.translationService.translate('common.cancel'),
      type: 'delete' as const
    };

    try {
      const confirmed = await this.confirmationModal.show(confirmData);
      
      if (!confirmed) {
        console.log('User cancelled deletion');
        return;
      }

      // Double-check before proceeding with deletion
      if (this.isDeleting || this.deletingLetterId === id) {
        console.log('Delete operation already in progress, skipping');
        return;
      }

      // User confirmed deletion
      this.isDeleting = true;
      this.deletingLetterId = id;
      
      console.log('Starting deletion of letter:', id);
      
      this.lettersService.deleteLetter(id).pipe(
        takeUntil(this.destroy$)
      ).subscribe({
        next: (res) => {
          console.log('Letter deleted successfully:', id);
          this.allLetters = this.allLetters.filter(l => l.id !== id);
          this.filteredLetters = this.filteredLetters.filter(l => l.id !== id);
          this.showSuccessMessage(this.translationService.translate('letters.delete_letter_success'));
          this.isDeleting = false;
          this.deletingLetterId = null;
        },
        error: (error) => {
          console.error('Error deleting letter:', error);
          const errorMessage = error.error?.message || error.message || 'Unknown error occurred';
          this.showErrorMessage(this.translationService.translate('common.error') + ': ' + errorMessage);
          this.isDeleting = false;
          this.deletingLetterId = null;
        }
      });
    } catch (error) {
      console.error('Error in delete confirmation:', error);
      this.isDeleting = false;
      this.deletingLetterId = null;
    }
  }

  async deleteAttachment(letterId: number, attachmentId: number, fileName: string) {
    // Check if already deleting
    if (this.isDeleting) {
      return;
    }

    const confirmData = {
      title: this.translationService.translate('letters.delete_attachment'),
      message: this.translationService.translate('letters.confirm_delete_attachment'),
      confirmText: this.translationService.translate('common.delete'),
      cancelText: this.translationService.translate('common.cancel'),
      type: 'delete' as const
    };

    try {
      const confirmed = await this.confirmationModal.show(confirmData);
      
      if (!confirmed) {
        console.log('User cancelled attachment deletion');
        return;
      }

      // User confirmed deletion
      this.isDeleting = true;
      this.lettersService.deleteAttachment(letterId, attachmentId).pipe(
        takeUntil(this.destroy$)
      ).subscribe({
        next: (res) => {
          // Update the letter's attachments
          const letter = this.allLetters.find(l => l.id === letterId);
          if (letter) {
            letter.attachments = letter.attachments.filter(a => a.id !== attachmentId);
          }
          const filteredLetter = this.filteredLetters.find(l => l.id === letterId);
          if (filteredLetter) {
            filteredLetter.attachments = filteredLetter.attachments.filter(a => a.id !== attachmentId);
          }
          this.showSuccessMessage(this.translationService.translate('letters.delete_attachment_success'));
          this.isDeleting = false;
        },
        error: (error) => {
          console.error('Error deleting attachment:', error);
          const errorMessage = error.error?.message || error.message || 'Unknown error occurred';
          this.showErrorMessage(this.translationService.translate('common.error') + ': ' + errorMessage);
          this.isDeleting = false;
        }
      });
    } catch (error) {
      console.error('Error in attachment delete confirmation:', error);
      this.isDeleting = false;
    }
  }

  showSuccessMessage(message: string) {
    const successData = {
      title: this.translationService.translate('common.success'),
      message: message,
      confirmText: this.translationService.translate('common.ok'),
      cancelText: '',
      type: 'info' as const
    };
    this.confirmationModal.show(successData);
  }

  showErrorMessage(message: string) {
    const errorData = {
      title: this.translationService.translate('common.error'),
      message: message,
      confirmText: this.translationService.translate('common.ok'),
      cancelText: '',
      type: 'warning' as const
    };
    this.confirmationModal.show(errorData);
  }

  toggleAttachments(letterId: number) {
    this.expandedAttachments[letterId] = !this.expandedAttachments[letterId];
  }

  editLetter(letter: LetterItem) {
    // Open edit modal with letter data
    this.editLetterModal.open(letter);
  }

  addAttachment(letter: LetterItem) {
    // Open add attachment modal
    this.addAttachmentModal.open(letter);
  }

  editAttachment(letter: LetterItem, attachment: any) {
    // Open edit attachment modal
    this.editAttachmentModal.open(letter, attachment);
  }

  showAttachmentDetails(letterId: number, attachment: any) {
    // Get attachment details from API
    this.lettersService.getLetterAttachments(letterId).subscribe({
      next: (res) => {
        if (res.success && res.data) {
          const attachmentDetails = res.data.find((a: any) => a.id === attachment.id);
          if (attachmentDetails) {
            this.showAttachmentDetailsModal(attachmentDetails);
          }
        }
      },
      error: (error) => {
        console.error('Error loading attachment details:', error);
        this.showErrorMessage(this.translationService.translate('common.error') + ': ' + (error.error?.message || error.message || 'Unknown error occurred'));
      }
    });
  }

  showAttachmentDetailsModal(attachment: any) {
    const detailsData = {
      title: this.translationService.translate('letters.attachment_details'),
      message: `
        <div class="attachment-details">
          <div class="row">
            <div class="col-md-6">
              <p><strong>${this.translationService.translate('letters.file_name')}:</strong> ${attachment.fileName || 'N/A'}</p>
              <p><strong>${this.translationService.translate('letters.file_type')}:</strong> ${attachment.fileType || 'N/A'}</p>
              <p><strong>${this.translationService.translate('letters.uploaded_by')}:</strong> ${attachment.uploadedByName || 'N/A'}</p>
            </div>
            <div class="col-md-6">
              <p><strong>${this.translationService.translate('letters.uploaded_at')}:</strong> ${this.formatDate(attachment.uploadedAt)}</p>
              <p><strong>${this.translationService.translate('letters.attachment_description')}:</strong> ${attachment.description || 'N/A'}</p>
            </div>
          </div>
        </div>
      `,
      confirmText: this.translationService.translate('common.close'),
      cancelText: '',
      type: 'info' as const
    };
    this.confirmationModal.show(detailsData);
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  get totalPages(): number {
    return Math.ceil(this.totalCount / this.pageSize);
  }

  get pages(): number[] {
    const pages: number[] = [];
    const maxVisiblePages = 5;
    
    if (this.totalPages === 0) {
      pages.push(1);
      return pages;
    }
    
    let startPage = Math.max(1, this.currentPage - Math.floor(maxVisiblePages / 2));
    let endPage = Math.min(this.totalPages, startPage + maxVisiblePages - 1);
    
    if (endPage - startPage + 1 < maxVisiblePages) {
      startPage = Math.max(1, endPage - maxVisiblePages + 1);
    }
    
    for (let i = startPage; i <= endPage; i++) {
      pages.push(i);
    }
    
    return pages;
  }

  get Math() {
    return Math;
  }

  formatDate(dateString: string): string {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleDateString();
  }

  getDirectionBadgeClass(direction: number): string {
    return direction === LetterDirection.In ? 'bg-primary' : 'bg-success';
  }

  getConfidentialityBadgeClass(confidentiality: number): string {
    switch (confidentiality) {
      case LetterConfidentiality.Public:
        return 'bg-success';
      case LetterConfidentiality.Restricted:
        return 'bg-warning';
      case LetterConfidentiality.Secret:
        return 'bg-danger';
      default:
        return 'bg-secondary';
    }
  }

  getStatusBadgeClass(status: number): string {
    switch (status) {
      case LetterStatus.Draft:
        return 'bg-secondary'; // 555555 - Gray for draft
      case LetterStatus.PendingApproval:
        return 'bg-warning'; // E54B1D - Orange for pending
      case LetterStatus.Approved:
        return 'bg-success'; // 4B5320 - Green for approved
      case LetterStatus.Rejected:
        return 'bg-danger'; // B45253 - Red for rejected
      case LetterStatus.Sent:
        return 'bg-info'; // 34495E - Blue for sent
      default:
        return 'bg-secondary'; // 555555 - Default gray
    }
  }

  // New approval workflow methods
  submitForApproval(letter: LetterItem) {
    if (this.isProcessing || this.processingLetterId === letter.id) {
      return;
    }

    this.isProcessing = true;
    this.processingLetterId = letter.id;
    
    this.lettersService.submitForApproval(letter.id).pipe(
      takeUntil(this.destroy$)
    ).subscribe({
      next: (res) => {
        console.log('Letter submitted for approval successfully');
        this.showSuccessMessage(this.translate('letters.submit_approval_success'));
        this.loadLetters(); // Reload to update status
        this.isProcessing = false;
        this.processingLetterId = null;
      },
      error: (error) => {
        console.error('Error submitting letter for approval:', error);
        const errorMessage = error.error?.message || error.message || 'Unknown error occurred';
        this.showErrorMessage(this.translate('common.error') + ': ' + errorMessage);
        this.isProcessing = false;
        this.processingLetterId = null;
      }
    });
  }

  sendEmail(letter: LetterItem) {
    if (this.isProcessing || this.processingLetterId === letter.id || this.sentEmails.has(letter.id)) {
      return;
    }

    this.isProcessing = true;
    this.processingLetterId = letter.id;
    
    this.lettersService.sendEmail(letter.id).pipe(
      takeUntil(this.destroy$)
    ).subscribe({
      next: (res) => {
        console.log('Email sent successfully');
        this.sentEmails.add(letter.id); // Mark email as sent
        this.showSuccessMessage(this.translate('letters.send_email_success'));
        this.isProcessing = false;
        this.processingLetterId = null;
      },
      error: (error) => {
        console.error('Error sending email:', error);
        const errorMessage = error.error?.message || error.message || 'Unknown error occurred';
        this.showErrorMessage(this.translate('common.error') + ': ' + errorMessage);
        this.isProcessing = false;
        this.processingLetterId = null;
      }
    });
  }

  downloadPdf(letter: LetterItem) {
    // Always try to download PDF for approved letters, regardless of pdfPath
    this.lettersService.downloadLetterPdf(letter.id).pipe(
      takeUntil(this.destroy$)
    ).subscribe({
      next: (blob) => {
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `Letter_${letter.id}.pdf`;
        link.click();
        window.URL.revokeObjectURL(url);
        this.showSuccessMessage(this.translate('letters.download_pdf_success'));
      },
      error: (error) => {
        console.error('Error downloading PDF:', error);
        if (error.status === 404) {
          this.showErrorMessage(this.translate('letters.no_pdf_available'));
        } else {
          const errorMessage = error.error?.message || error.message || 'Unknown error occurred';
          this.showErrorMessage(this.translate('common.error') + ': ' + errorMessage);
        }
      }
    });
  }

  canSubmitForApproval(letter: LetterItem): boolean {
    return letter.status === LetterStatus.Draft;
  }

  canSendEmail(letter: LetterItem): boolean {
    // Debug: Log the actual status value
    if (letter.status === 2) { // If status is 2 (Approved), log it
      console.log('Letter with status 2 (Approved):', letter.subject, 'Status:', letter.status, 'Expected Approved:', LetterStatus.Approved);
    }
    return letter.status === LetterStatus.Approved && !this.sentEmails.has(letter.id);
  }

  canDownloadPdf(letter: LetterItem): boolean {
    // Debug: Log the actual status value and PDF path
    if (letter.status === 2) { // If status is 2 (Approved), log it
      console.log('Letter with status 2 (Approved):', letter.subject, 'Status:', letter.status, 'PDF Path:', letter.pdfPath, 'Expected Approved:', LetterStatus.Approved);
    }
    return letter.status === LetterStatus.Approved && !!letter.pdfPath;
  }

  isProcessingLetter(letter: LetterItem): boolean {
    return this.isProcessing && this.processingLetterId === letter.id;
  }

  isEmailSent(letter: LetterItem): boolean {
    return this.sentEmails.has(letter.id);
  }

  getSendEmailButtonText(letter: LetterItem): string {
    if (this.isEmailSent(letter)) {
      return this.translate('letters.email_sent');
    }
    return this.translate('letters.send_email');
  }

  shouldShowPdfButton(letter: LetterItem): boolean {
    // Show PDF button for all approved letters
    // The downloadPdf method will handle the case where PDF is not available
    const shouldShow = letter.status === LetterStatus.Approved;
    // Debug logging
    if (letter.status === LetterStatus.Approved) {
      console.log('PDF Button Debug - Letter:', letter.subject, 'Status:', letter.status, 'Should Show:', shouldShow);
    }
    return shouldShow;
  }
}
