import { AfterViewInit, Component, ElementRef, ViewChild, EventEmitter, Output } from '@angular/core';
import { LettersService } from '../../../services/letters.service';
import { AbstractControl, FormBuilder, FormGroup, ValidationErrors, Validators } from '@angular/forms';
import { TranslationService } from '../../../services/translation.service';
import { LettersConfirmationModalComponent } from '../letters-confirmation-modal/letters-confirmation-modal.component';
import { UpdateLetterDto, LetterItem, LetterDirection, LetterConfidentiality, LetterStatus } from '../../../models/letter.model';

@Component({
  selector: 'app-edit-letter',
  standalone: false,
  templateUrl: './edit-letter.html',
  styleUrl: './edit-letter.css'
})
export class EditLetter implements AfterViewInit {
  @ViewChild('confirmationModal') confirmationModal!: LettersConfirmationModalComponent;
  @Output() letterUpdated = new EventEmitter<void>();
  
  // Custom modal properties
  isOpen = false;
  currentLetter: LetterItem | null = null;
  
  editForm!: FormGroup;
  
  constructor(
    private lettersService: LettersService,
    private fb: FormBuilder,
    private translationService: TranslationService
  ) {}

  ngAfterViewInit(): void {
    // No Bootstrap modal needed
  }
  
  open(letter: LetterItem) { 
    this.currentLetter = letter;
    this.isOpen = true;
    this.initializeForm();
    this.populateForm(letter);
  }
  
  close() { 
    this.isOpen = false;
    this.currentLetter = null;
    // Reset form when closing
    this.editForm.reset();
  }

  // Handle backdrop click
  onBackdropClick(event: Event) {
    if (event.target === event.currentTarget) {
      this.close();
    }
  }

  readonly directions = [
    { key: LetterDirection.In, value: "Incoming" },
    { key: LetterDirection.Out, value: "Outgoing" }
  ];

  readonly confidentialities = [
    { key: LetterConfidentiality.Public, value: "Public" },
    { key: LetterConfidentiality.Restricted, value: "Restricted" },
    { key: LetterConfidentiality.Secret, value: "Secret" }
  ];

  readonly statuses = [
    { key: LetterStatus.Draft, value: "Draft" },
    { key: LetterStatus.PendingApproval, value: "PendingApproval" },
    { key: LetterStatus.Approved, value: "Approved" },
    { key: LetterStatus.Rejected, value: "Rejected" },
    { key: LetterStatus.Sent, value: "Sent" }
  ];

  ngOnInit() {
    this.initializeForm();
  }

  initializeForm() {
    this.editForm = this.fb.group({
      direction: ['', Validators.required],
      subject: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(200)]],
      body: ['', [Validators.required, Validators.minLength(10), Validators.maxLength(500)]],
      to: ['', [Validators.required, Validators.email]],
      cc: [''],
      bcc: [''],
      letterDate: ['', Validators.required],
      referenceNumbers: ['']
    });
  }

  populateForm(letter: LetterItem) {
    // Format the date for the date input
    const letterDate = new Date(letter.letterDate);
    const formattedDate = letterDate.toISOString().split('T')[0];
    
    this.editForm.patchValue({
      direction: letter.direction,
      subject: letter.subject,
      body: letter.body,
      to: letter.to,
      cc: letter.cc || '',
      bcc: letter.bcc || '',
      letterDate: formattedDate,
      referenceNumbers: letter.referenceNumbers || ''
    });
  }

  isFieldValid(field: string) {
    const control = this.editForm.get(field);
    return control?.touched && control?.invalid;
  }

  hasServerError(field: string) {
    const control = this.editForm.get(field);
    return control?.errors?.['serverError'];
  }

  getServerErrorMessage(field: string) {
    const control = this.editForm.get(field);
    return control?.errors?.['serverError'] || '';
  }

  getErrorMessage(field: string) {
    const control = this.editForm.get(field);
    if (!control?.errors) return '';
    
    if (control.errors['required']) {
      return this.translate('common.field_required');
    }
    if (control.errors['email']) {
      return this.translate('common.invalid_email');
    }
    if (control.errors['minlength']) {
      const requiredLength = control.errors['minlength']['requiredLength'];
      if (requiredLength === 3) {
        return this.translate('letters.min_length_3');
      } else if (requiredLength === 10) {
        return this.translate('letters.min_length_10');
      }
      return `Minimum length is ${requiredLength} characters`;
    }
    if (control.errors['maxlength']) {
      const requiredLength = control.errors['maxlength']['requiredLength'];
      if (requiredLength === 200) {
        return this.translate('letters.max_length_200');
      } else if (requiredLength === 500) {
        return this.translate('letters.max_length_500');
      }
      return `Maximum length is ${requiredLength} characters`;
    }
    return 'Invalid field';
  }

  onSubmit() {
    if (this.editForm.invalid || !this.currentLetter) {
      this.editForm.markAllAsTouched();
      return;
    }

    const formValue = this.editForm.value;
    
    // Ensure all required fields are included and properly formatted
    const letterData: UpdateLetterDto = {
      id: this.currentLetter.id,
      direction: parseInt(formValue.direction),
      subject: formValue.subject,
      body: formValue.body,
      to: formValue.to,
      cc: formValue.cc || '',
      bcc: formValue.bcc || '',
      letterDate: new Date(formValue.letterDate).toISOString(),
      referenceNumbers: formValue.referenceNumbers || ''
    };

    console.log('Updating letter data:', letterData); // Debug log

    this.lettersService.updateLetter(this.currentLetter.id, letterData).subscribe({
      next: (res) => {
        this.showSuccessMessage(this.translate('letters.edit_letter_success'));
        this.close();
        this.editForm.reset();
        this.letterUpdated.emit(); // Notify parent component
      },
      error: (error) => {
        console.error('Error updating letter:', error);
        this.handleApiErrors(error);
      }
    });
  }

  private handleApiErrors(error: any) {
    console.log('Full error response:', error); // Debug log
    
    if (error.error?.errors) {
      // Handle validation errors by setting them on form controls
      const errors = error.error.errors;
      console.log('API validation errors:', errors); // Debug log
      
      Object.keys(errors).forEach(fieldName => {
        const control = this.editForm.get(this.getFormFieldName(fieldName));
        if (control) {
          control.setErrors({ serverError: errors[fieldName][0] });
          control.markAsTouched();
        } else {
          console.warn(`Form control not found for field: ${fieldName}`); // Debug log
        }
      });
    } else {
      // Show generic error in alert
      let errorMessage = 'Unknown error occurred';
      if (error.error?.message) {
        errorMessage = error.error.message;
      } else if (error.message) {
        errorMessage = error.message;
      }
      alert(this.translationService.translate('common.error') + ': ' + errorMessage);
    }
  }

  private getFormFieldName(apiFieldName: string): string {
    // Map API field names (capitalized) to form field names (lowercase)
    const fieldMapping: { [key: string]: string } = {
      'Direction': 'direction',
      'Subject': 'subject',
      'Body': 'body',
      'Confidentiality': 'confidentiality',
      'To': 'to',
      'Cc': 'cc',
      'Bcc': 'bcc',
      'LetterDate': 'letterDate',
      'Status': 'status',
      'ReferenceNumbers': 'referenceNumbers'
    };
    return fieldMapping[apiFieldName] || apiFieldName.toLowerCase();
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  showSuccessMessage(message: string) {
    const successData = {
      title: this.translate('common.success'),
      message: message,
      confirmText: this.translate('common.ok'),
      cancelText: '',
      type: 'info' as const
    };
    this.confirmationModal.show(successData);
  }

  getDirectionLabel(direction: number): string {
    return this.translate('letters.directions.' + direction);
  }

  getConfidentialityLabel(confidentiality: number): string {
    return this.translate('letters.confidentialities.' + confidentiality);
  }

  getStatusLabel(status: number): string {
    return this.translate('letters.statuses.' + status);
  }
}
