import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LettersList } from './letters-list/letters-list';
import { LettersApprovalComponent } from './letters-approval/letters-approval.component';

const routes: Routes = [
  {
    path: '',
    component: LettersList
  },
  {
    path: 'approval',
    component: LettersApprovalComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LettersRoutingModule { }
