import { AfterViewInit, Component, ElementRef, ViewChild, EventEmitter, Output } from '@angular/core';
import { LettersService } from '../../../services/letters.service';
import { AbstractControl, FormBuilder, FormGroup, ValidationErrors, Validators } from '@angular/forms';
import { TranslationService } from '../../../services/translation.service';
import { LettersConfirmationModalComponent } from '../letters-confirmation-modal/letters-confirmation-modal.component';
import { LetterItem, AttachmentUploadDto } from '../../../models/letter.model';

@Component({
  selector: 'app-add-attachment',
  standalone: false,
  templateUrl: './add-attachment.html',
  styleUrl: './add-attachment.css'
})
export class AddAttachment implements AfterViewInit {
  @ViewChild('confirmationModal') confirmationModal!: LettersConfirmationModalComponent;
  @ViewChild('fileInput') fileInput!: ElementRef<HTMLInputElement>;
  @Output() attachmentAdded = new EventEmitter<void>();
  
  // Custom modal properties
  isOpen = false;
  currentLetter: LetterItem | null = null;
  selectedFile: File | null = null;
  isUploading = false;
  
  addForm!: FormGroup;
  
  // Supported file types
  readonly supportedFileTypes = [
    'application/pdf',
    'application/msword',
    'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
    'application/vnd.ms-excel',
    'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
    'application/vnd.ms-powerpoint',
    'application/vnd.openxmlformats-officedocument.presentationml.presentation',
    'image/jpeg',
    'image/png',
    'image/gif'
  ];

  readonly maxFileSize = 10 * 1024 * 1024; // 10MB
  
  constructor(
    private lettersService: LettersService,
    private fb: FormBuilder,
    private translationService: TranslationService
  ) {}

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  ngAfterViewInit(): void {
    // No Bootstrap modal needed
  }
  
  open(letter: LetterItem) { 
    this.currentLetter = letter;
    this.isOpen = true;
    this.initializeForm();
    this.resetForm();
  }
  
  close() { 
    this.isOpen = false;
    this.currentLetter = null;
    this.selectedFile = null;
    this.isUploading = false;
    this.resetForm();
  }

  // Handle backdrop click
  onBackdropClick(event: Event) {
    if (event.target === event.currentTarget) {
      this.close();
    }
  }

  ngOnInit() {
    this.initializeForm();
  }

  initializeForm() {
    this.addForm = this.fb.group({
      description: ['', [Validators.maxLength(200)]],
      documentSource: ['']
    });
  }

  resetForm() {
    this.addForm.reset();
    this.selectedFile = null;
    if (this.fileInput) {
      this.fileInput.nativeElement.value = '';
    }
  }

  onFileSelected(event: any) {
    const file = event.target.files[0];
    if (file) {
      // Validate file type
      if (!this.supportedFileTypes.includes(file.type)) {
        this.showErrorMessage(this.translate('letters.file_format_error'));
        this.resetForm();
        return;
      }

      // Validate file size
      if (file.size > this.maxFileSize) {
        this.showErrorMessage(this.translate('letters.file_size_error'));
        this.resetForm();
        return;
      }

      this.selectedFile = file;
    } else {
      this.selectedFile = null;
    }
  }

  onDragOver(event: DragEvent) {
    event.preventDefault();
    event.stopPropagation();
  }

  onDragLeave(event: DragEvent) {
    event.preventDefault();
    event.stopPropagation();
  }

  onDrop(event: DragEvent) {
    event.preventDefault();
    event.stopPropagation();
    
    const files = event.dataTransfer?.files;
    if (files && files.length > 0) {
      const file = files[0];
      
      // Validate file type
      if (!this.supportedFileTypes.includes(file.type)) {
        this.showErrorMessage(this.translate('letters.file_format_error'));
        return;
      }

      // Validate file size
      if (file.size > this.maxFileSize) {
        this.showErrorMessage(this.translate('letters.file_size_error'));
        return;
      }

      this.selectedFile = file;
      if (this.fileInput) {
        this.fileInput.nativeElement.files = files;
      }
    }
  }

  removeFile() {
    this.selectedFile = null;
    if (this.fileInput) {
      this.fileInput.nativeElement.value = '';
    }
  }

  isFieldValid(field: string) {
    const control = this.addForm.get(field);
    return control?.touched && control?.invalid;
  }

  hasServerError(field: string) {
    const control = this.addForm.get(field);
    return control?.errors?.['serverError'];
  }

  getServerErrorMessage(field: string) {
    const control = this.addForm.get(field);
    return control?.errors?.['serverError'] || '';
  }

  getErrorMessage(field: string) {
    const control = this.addForm.get(field);
    if (!control?.errors) return '';
    
    if (control.errors['required']) {
      return this.translate('common.field_required');
    }
    if (control.errors['maxlength']) {
      const requiredLength = control.errors['maxlength']['requiredLength'];
      return `Maximum length is ${requiredLength} characters`;
    }
    return 'Invalid field';
  }

  onSubmit() {
    if (!this.selectedFile || !this.currentLetter) {
      this.showErrorMessage(this.translate('letters.file_required'));
      return;
    }

    if (this.addForm.invalid) {
      this.addForm.markAllAsTouched();
      return;
    }

    this.isUploading = true;
    const formValue = this.addForm.value;
    
    const attachmentData: AttachmentUploadDto = {
      file: this.selectedFile,
      description: formValue.description || undefined,
      documentSource: formValue.documentSource ? parseInt(formValue.documentSource) : undefined
    };

    console.log('Uploading attachment:', attachmentData); // Debug log

    this.lettersService.addAttachment(this.currentLetter.id, attachmentData).subscribe({
      next: (res) => {
        this.isUploading = false;
        this.showSuccessMessage(this.translate('letters.add_attachment_success'));
        this.close();
        this.attachmentAdded.emit(); // Notify parent component
      },
      error: (error) => {
        this.isUploading = false;
        console.error('Error adding attachment:', error);
        this.handleApiErrors(error);
      }
    });
  }

  private handleApiErrors(error: any) {
    console.log('Full error response:', error); // Debug log
    
    if (error.error?.errors) {
      // Handle validation errors by setting them on form controls
      const errors = error.error.errors;
      console.log('API validation errors:', errors); // Debug log
      
      Object.keys(errors).forEach(fieldName => {
        const control = this.addForm.get(this.getFormFieldName(fieldName));
        if (control) {
          control.setErrors({ serverError: errors[fieldName][0] });
          control.markAsTouched();
        } else {
          console.warn(`Form control not found for field: ${fieldName}`); // Debug log
        }
      });
    } else {
      // Show generic error
      let errorMessage = 'Unknown error occurred';
      if (error.error?.message) {
        errorMessage = error.error.message;
      } else if (error.message) {
        errorMessage = error.message;
      }
      this.showErrorMessage(this.translate('common.error') + ': ' + errorMessage);
    }
  }

  private getFormFieldName(apiFieldName: string): string {
    // Map API field names (capitalized) to form field names (lowercase)
    const fieldMapping: { [key: string]: string } = {
      'Description': 'description',
      'DocumentSource': 'documentSource'
    };
    return fieldMapping[apiFieldName] || apiFieldName.toLowerCase();
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  showSuccessMessage(message: string) {
    const successData = {
      title: this.translate('common.success'),
      message: message,
      confirmText: this.translate('common.ok'),
      cancelText: '',
      type: 'info' as const
    };
    this.confirmationModal.show(successData);
  }

  showErrorMessage(message: string) {
    const errorData = {
      title: this.translate('common.error'),
      message: message,
      confirmText: this.translate('common.ok'),
      cancelText: '',
      type: 'warning' as const
    };
    this.confirmationModal.show(errorData);
  }

  formatFileSize(bytes: number): string {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  }

  getFileIcon(fileType: string): string {
    if (fileType.includes('pdf')) return 'fas fa-file-pdf text-danger';
    if (fileType.includes('word') || fileType.includes('document')) return 'fas fa-file-word text-primary';
    if (fileType.includes('excel') || fileType.includes('spreadsheet')) return 'fas fa-file-excel text-success';
    if (fileType.includes('powerpoint') || fileType.includes('presentation')) return 'fas fa-file-powerpoint text-warning';
    if (fileType.includes('image')) return 'fas fa-file-image text-info';
    return 'fas fa-file text-secondary';
  }
}
